.section1 {
    width: 100%;
    height: auto;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background: rgba(29, 28, 29, 0);
  }
  
  .section1:before {
    content: '';
    position: absolute;
    /* background: url('../../assets/bg4.png') no-repeat center center/cover; */
    height: 100%;
    width: 100%;
    z-index: -1;
    opacity: .08;
  }

.section1-content {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 1rem;
    padding: 5rem 1rem;
}

.section1-left {
    width: 550px;
    margin: auto;
}

.section1-left img {
    width: 100%;
    height: auto;
}

.section1-right {
    width: 50%;
    margin: auto;
}


.section1-right h1 {
    font-size: 1rem;
    text-transform: uppercase;
    padding: 1rem 0;
}


.section1-right p {
    text-align: justify;
    font-family: 'Roboto', sans-serif;
    color: var(--primary-text);
    font-size: 16px;
}


.section1-link {
    text-align: center;    
}

.section1-link p span {
    font-size: 16px;
    color: var(--primary-color);
}

.section1-link p span:hover {
    color: orange;
}



@media screen and (max-width:940px)  {

    .section1-content {
        flex-wrap: wrap;
    }
    

    .section1-right {
        width: 90%;
    }
    
    .section1-right h1 {
        text-align: center;
    }
    
}

